export default {
	data() {
		return {
			
		};
	},
	computed:{
		height(){
			const {platform,statusBarHeight} = uni.getSystemInfoSync()
			let height = statusBarHeight +4 //ios 24px
			if (platform.toLowerCase() == "android" ){
				height +=4 //android 28px
			}
			// 胶囊高度 32px 下边框6px height 状态栏高度
			return height+ 38 + "px"
		},
		marginTop(){
			const {platform,statusBarHeight} = uni.getSystemInfoSync()
			let height = statusBarHeight +4
			if (platform.toLowerCase() == "android" ){
				height +=4
			}
			return height + "px"				
		}
	},
	updated() {
		console.log(this.config)
	},
	mounted() {
		console.log(this.config)
		
	},
	props:{
		config:{
			type:Object,
			default(){
				return {
					title:"",
					bgcolor:"",
					type:2,
					linear:false,
					transparent:false,
					fontcolor:"#000",
					menuIcon:"",
					menuText:""
				}
			}
		},
		scrollTop:{
			type:Number,
			default:0
		},
		scrollMaxHeight:{
			type:Number,
			default:288
		}
	},
	methods:{
		back_(){
			uni.navigateBack({
				delta:1
			});
		},
		home_(){
			uni.switchTab({
				url:"/pages/index/index"
			})
		},
		conduct_(){
			this.$emit("customConduct")
		}
	}
}
